<template>
    <div class="HelpParticulars">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <p class="title"><i></i><span>{{name}}</span></p>
      <div class="ParticularsHome">
        <p v-for="(data, index) in Home" @click="HelpContent">{{data}}</p>





      </div>

    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "HelpParticulars",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: { name: "认证中心",left: true},
              Home:[],
              name:"",
            }
        },
        methods: {
          HelpContent(){
            this.$router.push({
              path:"HelpContent",
            })

          },

        },
        created() {
          console.log(this.$route.params);
          this.Home=this.$route.params.Home;
          this.name=this.$route.params.name

        }
    }

</script>

<style lang="less">
  .HomePageTop .Head{ background: #fff;}
  .title{   margin-top: .24rem;   background: #fff;   height: 1.02rem;    padding:0 .3rem ;  border-bottom: 1px solid #E3E4E8; line-height: 1.02rem;
    i{    vertical-align: middle;    display: inline-block;     width: .08rem;   height: .28rem; background: #f90; }
    span{font-size: .34rem; display: inline-block;vertical-align: middle;margin-left: .24rem;}
  }
  .ParticularsHome{
    background: #fff;
    padding:0 .3rem ;
    p{
      background: #fff;
      border-bottom: 1px solid #E3E4E8;
      color: #333;
      font-size: .34rem;
      height: 1.02rem;
      line-height: 1.02rem;
      overflow: hidden; // 超出的文本隐藏
      text-overflow: ellipsis; // 溢出用省略号显示
      white-space: nowrap; // 溢出不换行
    }
  }
</style>



